nav {
	background: var(--black);
	padding: 1rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-size: 1.2rem;
	width: 100%;
	position: relative;
	z-index: 1;
	border-bottom: 2px solid var(--gold);
	box-shadow: 0 0.5rem 0.5rem rgba(0, 0, 0, 0.2);
}

.home-link {
	height: calc(4rem - 2px);
	display: flex;
	align-items: center;
	color: #fff;
	text-decoration: none;
}

.logo {
	display: flex;
	height: 100%;
	width: 100%;
}

.nav-title {
	margin: 0 0 0 1rem;
	white-space: nowrap;
}

.fa-leaf {
	margin-left: 0.5rem;
	font-size: 1.6rem;
	color: greenyellow;
}

.fa-bars {
	color: #fff;
}

.hamburger {
	display: block;
	cursor: pointer;
}

.nav-menu {
	position: absolute;
	z-index: 999;
	top: 100%;
	left: 0;
	width: 100%;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	list-style: none;
	height: calc(100vh - 6rem);
	background: var(--gray-100);
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
	transition: -webkit-transform 750ms ease-in-out;
	transition: transform 750ms ease-in-out;
	transition: transform 750ms ease-in-out, -webkit-transform 750ms ease-in-out;
}

.nav-menu__active {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
}

.nav-menu li {
	display: grid;
	place-items: center;
	height: 25%;
	width: 100%;
	position: relative;
}

.nav-menu li::before {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 0%;
	background: var(--bronze);
	z-index: -1;
	transition: height 250ms ease-out;
}

.nav-menu li:hover::before {
	height: 100%;
}

.nav-menu a {
	color: #fff;
	text-decoration: none;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	transition: color 250ms ease-out;
}

.nav-menu li:hover a {
	color: var(--black);
}

@media (min-width: 960px) {
	.hamburger {
		display: none;
	}

	.nav-menu {
		position: static;
		width: auto;
		margin: 0;
		padding: 0;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		list-style: none;
		height: 100%;
		background: transparent;
		-webkit-transform: none;
		        transform: none;
		transition: -webkit-transform 0ms;
		transition: transform 0ms;
		transition: transform 0ms, -webkit-transform 0ms;
	}

	.nav-menu__active {
		-webkit-transform: none;
		        transform: none;
	}

	.nav-menu li {
		height: auto;
		padding: 0.5rem 1rem;
		margin: 0;
		border-top: solid 2px var(--gold);
		border-bottom: solid 2px var(--gold);
	}

	.nav-menu li:nth-child(2n) {
		border-left: 2px solid var(--gold);
		border-right: 2px solid var(--gold);
	}

	.nav-menu li:first-child {
		border-left: 2px solid var(--gold);
		border-top-left-radius: 0.25rem;
		border-bottom-left-radius: 0.25rem;
	}
	.nav-menu li:last-child {
		border-right: 2px solid var(--gold);
		border-top-right-radius: 0.25rem;
		border-bottom-right-radius: 0.25rem;
	}
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  pointer-events: none;
  overflow: hidden;
  z-index: 9998;
}

.modal.enter-done {
  opacity: 1;
  pointer-events: visible;
}

.modal.exit {
  opacity: 0;
}

.modal-content {
  width: 50%;
  background-color: #fff;
  transition: all 0.3s ease-in-out;
  -webkit-transform: translateY(-200px);
          transform: translateY(-200px);
  color: green;
}

.modal.enter-done .modal-content {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.modal.exit .modal-content {
  -webkit-transform: translateY(-200px);
          transform: translateY(-200px);
}

.modal-header,
.modal-footer {
  padding: 10px;
}

.modal-title {
  margin: 0;
}

.modal-body {
  padding: 10px;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}

